import React, { useState } from "react";
import { Anchor, TextInput } from "grommet";
import Parse, { createAccount } from "../../library/parse";
import Box from "../../components/Box";
import Button from "../../components/Button";

const Component = () => {
  const [model, setModel] = useState({});
  const [message, setMessage] = useState("");
  return (
    <Box margin="0">
      Name:
      <TextInput
        value={model["fullname"]}
        onChange={(event) => {
          setModel({
            ...model,
            fullName: event.target.value,
          });
        }}
        placeholder="Name"
      />
      Email:
      <TextInput
        value={model["email"]}
        onChange={(event) => {
          setModel({
            ...model,
            username: event.target.value,
            email: event.target.value,
          });
        }}
        placeholder="email"
      />
      Password:
      <TextInput
        value={model["password"]}
        onChange={(event) => {
          setModel({
            ...model,
            password: event.target.value,
          });
        }}
        type="password"
        placeholder="Password"
      />
      <Box direction="row">
        <Button
          label="Create Account"
          primary
          message={message}
          onClick={() => {
            if (model.fullName && model.email && model.password) {
              if (model.password.split("").length < 8) {
                setMessage("Password should be at least 8 characters");
              } else {
                return createAccount(model)
                  .then((e) => {
                    setMessage("Account Created. Please verify your email");
                    setMessage(typeof e);
                  })
                  .catch((e) => {
                    setMessage(e.message);
                  });
              }
            }
          }}
        />
      </Box>
    </Box>
  );
};
export default Component;
