import React, { useState } from "react";
import { TextInput, Anchor } from "grommet";
import Button from "../../components/Button";
import { logIn } from "../../library/parse";
import { assign } from "../../helpers/index";
import Box from "../../components/Box";

const Component = () => {
  const [model, setModel] = useState({});
  const [message, setMessage] = useState("");
  return (
    <Box margin="0" direction="column">
      <Box>
        Email:
        <TextInput
          value={model["email"]}
          onChange={(event) => {
            setModel({
              ...model,
              username: event.target.value,
              email: event.target.value,
            });
          }}
          placeholder="email"
        />
      </Box>
      <Box>
        Password:
        <TextInput
          value={model["password"]}
          onChange={(event) => {
            setModel({
              ...model,
              password: event.target.value,
            });
          }}
          type="password"
          placeholder="password"
        />
      </Box>
      <Box>
        <Anchor href="/reset"> Reset Password </Anchor>
      </Box>
      <Box direction="row">
        <Button
          message={message}
          label="Login"
          primary
          onClick={() => {
            if (model.email && model.password) {
              return logIn(model.email, model.password)
                .then((e) => assign("/"))
                .catch((e) => setMessage(e.message));
            } else {
              return setMessage("Please fill up username/password");
            }
          }}
        />
      </Box>
    </Box>
  );
};
export default Component;
